.articles {
  background: rgba(15, 15, 15, 0.9);
  backdrop-filter: blur(10px);
  -webkit-backdrop-filter: blur(10px);
  padding: 4.5rem 5rem 7rem;
  color: white;
  width: 100%;
  scroll-snap-align: start;
  border: 1px solid rgba(255, 255, 255, 0.1);
}

.articles .title {
  text-align: center;
  color: #fff;
  font-family: monospace;
  font-size: 35px;
  padding-bottom: 35px;
}

/* Font */
@import url('https://fonts.googleapis.com/css?family=Quicksand:400,700');

/* Design */
main,
main::before,
main::after {
  box-sizing: border-box;
}


.articles .main {
  max-width: 1200px;
  margin: 0 auto;
  color: #272727;
  font-family: 'Quicksand', serif;
  font-style: normal;
  font-weight: 400;
  letter-spacing: 0;
  padding: 1rem;

}

.articles img {
  height: auto;
  min-width: 100%;
  max-width: 100%;
  vertical-align: middle;
}



.articles .cards {
  display: flex;
  flex-wrap: wrap;
  list-style: none;
  margin: 0;
  padding: 0;
  justify-content: center;
}

.articles .card {
  /* Glass / frosted effect */
  background: rgba(95, 95, 95, 0.05);
  backdrop-filter: blur(12px);
  -webkit-backdrop-filter: blur(12px);
  /* Rounded + glossy border */
  border-radius: 20px;
  border: 1px solid rgba(255, 255, 255, 0.179);
  /* Shadow / depth */
  box-shadow: 0 8px 25px rgba(0, 0, 0, 0.3), inset 0 0 15px rgba(255, 255, 255, 0.05);
  border-radius: 20px;

  display: flex;
  flex-direction: column;
  overflow: hidden;
  transition: all .2s ease-in-out;
  position: relative;
}

.articles .card:hover {
  border: 1px solid rgba(2, 133, 2, 0.89);
  transform: scale(1.01);
  box-shadow: 0 0 15px rgba(2, 133, 2, 0.89);
  box-shadow: 0 8px 25px rgba(2, 133, 2, 0.89), inset 0 0 15px rgba(255, 255, 255, 0.05);
}


.articles .cards_item {
  display: flex;
  padding: 1rem;
  height: 400px;
}

@media (min-width: 40rem) {
  .articles .cards_item {
    width: 50%;
  }
}

@media (min-width: 56rem) {
  .articles .cards_item {
    width: 30%;
  }
}

@media only screen and (min-width: 300px) {

  .articles {
    padding: 4.5rem 1rem;
  }

}


.articles .card_content {
  padding: 1rem;
  background: transparent;

}

.articles .card_title {
  color: #ffffff;
  font-size: 1.1rem;
  font-weight: 700;
  letter-spacing: 1px;
  text-transform: capitalize;
  margin: 0px;
}

.articles .card_text {
  color: #ffffff;
  font-size: 0.875rem;
  line-height: 1.5;
  margin-bottom: 1.25rem;
  font-weight: 400;
}

.articles .link_button {
  position: absolute;
  bottom: 10px;
  left: 50%;
  margin: 0;
  -ms-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
}

.articles .btn {
  color: #ffffff;
  padding: 0.8rem;
  padding-left: 40px;
  padding-right: 40px;
  font-size: 14px;
  text-transform: uppercase;
  border-radius: 10px;
  font-weight: 400;
  display: block;
  width: 100%;
  cursor: pointer;
  border: 1px solid rgba(255, 255, 255, 0.2);
  background: transparent;
  text-decoration: none;
  text-align: center;
  transition: all .3s ease-in-out;


}

.articles .card:hover .btn {
  border: 1px solid rgba(2, 133, 2, 0.89);
  color: rgb(12, 210, 12);
}

.articles .btn:hover {
  background-color: rgba(2, 133, 2, 0.89);
  color: white !important;
  
}