.about-me {
    background-color: rgba(20, 20, 20, 0.6);
    /* semi-transparent for glass effect */
    backdrop-filter: blur(6px);
    /* subtle blur */
    -webkit-backdrop-filter: blur(6px);
    /* for Safari support */

    padding: 4.5rem 5rem 7rem;
    color: white;
    width: 100%;
    scroll-snap-align: start;
    border: 1px solid rgba(255, 255, 255, 0.1);
    /* optional, subtle border for glass edges */
}

.about-me h1 {
    text-align: center;
    color: #fff;
    font-family: monospace;
    font-size: 35px;
    padding-bottom: 4rem;
}

.center_align {
    justify-content: center;
    display: flex;
}

.about-me-contents {
    display: flex;
    align-items: center;
    /* vertically align image with text */
    justify-content: center;
    width: 70%;
    margin: 0 auto;
    position: relative;
}

/* Left section (profile picture) */
.about-me .left-section {
    flex: 0 0 250px;
    /* fix image width */
    margin-right: -80px;
    /* creates constant overlap */
    text-align: center;
    position: relative;
    z-index: 2;
    display: flex;
    align-items: center;
    justify-content: center;
}

/* Profile picture */
.about-me .left-section img {
    width: 250px;
    border-radius: 20px;
    box-shadow: 0 6px 8px 0 rgba(0, 0, 0, 0.304),
        0 6px 20px 0 rgba(0, 0, 0, 0.479);
    transition: all .3s ease-in-out;
    background-color: rgb(37, 41, 36);
    position: relative;
    z-index: 2;
}

.about-me .left-section img:hover {
    box-shadow: 0 0 15px rgba(17, 252, 17, 0.89);
    transform: scale(1.02);
    /* smoother hover */
}

/* Right section (About Me content) */
.about-me .right-section {
    flex: 1;
    /* take remaining width */
    padding: 2rem 3.5rem 2rem 7rem;
    /* push text right so it never clashes with image */
    font-family: "Calibre", "Inter", "San Francisco", "SF Pro Text", -apple-system, system-ui, sans-serif;
    font-size: 18px;
    font-weight: 400;
    /* background-color: #252525; */
    border-radius: 15px;
    transition: all .2s ease-in-out;
    box-shadow: 0 6px 8px 0 rgba(0, 0, 0, 0.304),
        0 6px 20px 0 rgba(0, 0, 0, 0.479);
    position: relative;
    z-index: 1;

    /* Glass / frosted effect */
    background: rgba(53, 48, 48, 0.05);
    backdrop-filter: blur(12px);
    -webkit-backdrop-filter: blur(12px);
    /* Rounded + glossy border */
    border-radius: 20px;
    border: 1px solid rgba(255, 255, 255, 0.25);
    /* Shadow / depth */
    box-shadow: 0 8px 25px rgba(0, 0, 0, 0.3), inset 0 0 15px rgba(255, 255, 255, 0.05);
}

.about-me .right-section:hover {
    
  backdrop-filter: blur(14px);
  -webkit-backdrop-filter: blur(14px);
}

/* Skills list */
ul.skills-list {
    display: grid;
    grid-template-columns: repeat(3, minmax(120px, 1fr));
    /* 4 equal columns */
    grid-auto-rows: auto;
    gap: 10px 20px;
    /* row-gap, col-gap */
    padding: 0;
    margin: 20px 0 0 0;
    list-style: none;
}

ul.skills-list li {
    position: relative;
    margin-bottom: 10px;
    padding-left: 20px;
    font-family: monospace;
    font-size: 16px;
    white-space: nowrap;
    /* keeps items tidy */
}

ul.skills-list li::before {
    content: '▶';
    position: absolute;
    left: 0;
    color: rgb(14, 229, 14);
    font-size: 10px;
    line-height: 20px;
}


/* Responsive fix */
@media only screen and (max-width: 1240px) {
    .about-me {
        padding: 0;
    }

    .about-me h1 {
        margin-top: 40px;
    }

    .about-me-contents {
        display: block;
        width: 100%;
    }

    .about-me .left-section {
        width: 250px;
        /* fixed / small width for profile pic */
        max-width: 80%;
        /* responsive cap */
        margin: 0 auto;
        /* center the image */
        padding: 1.5rem 0;
        text-align: center;
        position: relative;
        z-index: 2;
    }

    .about-me .right-section {
        width: 90%;
        /* take 90% of screen width */
        max-width: 90%;
        margin: 1rem auto 0 auto;
        /* center horizontally */
        padding: 1.5rem;
        box-sizing: border-box;
        text-align: left;
        z-index: 1;
    }

    ul.skills-list {
        display: grid;
        grid-template-columns: repeat(2, minmax(120px, 1fr));
        /* 4 equal columns */
        grid-auto-rows: auto;
        gap: 10px 20px;
        /* row-gap, col-gap */
        padding: 0;
        margin: 20px 0 0 0;
        list-style: none;
    }

    ul.skills-list li {
        font-size: 0.85rem;
    }
}