.background-image {
  position: fixed;
  inset: 0px;
  box-sizing: border-box;
  padding: 0px;
  border: none;
  margin: auto;
  display: block;
  width: 0px;
  height: 0px;
  min-width: 100%;
  max-width: 100%;
  min-height: 100%;
  max-height: 100%;
  object-fit: cover;
  z-index: -1;

}


.hero-container {
  height: 100vh;
  width: 100%;
  margin-top: -60px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  box-shadow: inset 0 0 0 1000px rgba(0, 0, 0, 0.2);
  object-fit: contain;
}
.welcome-text {
  max-width: 1024px;
  height: 425px;
  display: flex;
  flex-direction: column;
  align-items: center;

  /* Glass / frosted effect */
  background: rgba(255, 255, 255, 0.05); /* very subtle transparency */
  backdrop-filter: blur(12px); /* blur background behind */
  -webkit-backdrop-filter: blur(12px); /* Safari support */
  
  /* Rounded + glossy border */
  border-radius: 20px;
  border: 1px solid rgba(255, 255, 255, 0.25); /* glossy edge */
  
  /* Shadow / depth */
  box-shadow: 0 8px 25px rgba(0, 0, 0, 0.3), inset 0 0 15px rgba(255, 255, 255, 0.05);
  transition: all 0.3s ease;
}

.welcome-text:hover {
  transform: translateY(-1px) scale(1.01); /* subtle lift and expansion */
  box-shadow: 
    0 30px 100px rgba(16, 225, 16, 0.25), /* stronger expanded outer glow */
    0 20px 50px rgba(0, 0, 0, 0.35),       /* deeper shadow for contrast */
    inset 0 0 30px rgba(16, 225, 16, 0.1); /* inner highlight */
  
  backdrop-filter: blur(14px);
  -webkit-backdrop-filter: blur(14px);
}


/* Headings and paragraph remain mostly the same, just ensure contrast */
.welcome-text > h1 {
  color: #fff;
  font-size: 2.2rem;
  font-family: monospace;
  margin-top: 30px;
  text-shadow: 0 2px 4px rgba(0,0,0,0.5);
}

.welcome-text h2 .code-font {
  color: rgb(16, 225, 16);
}

green {
  color: rgb(16, 225, 16);
}

.welcome-text > h2 {
  margin-top: -0.7rem;
  color: #fff;
  font-size: 4rem;
  font-family: system-ui;
  text-shadow: 0 2px 6px rgba(0,0,0,0.5);
}

.welcome-text > p {
  margin-top: 2rem;
  color: #fff;
  font-size: 1.1rem;
  line-height: 1.75;
  font-family: system-ui;
  padding: 0px 35px;
  text-align: center;
  text-shadow: 0 1px 3px rgba(0,0,0,0.4);
}


.hero-btns {
  margin-top: 32px;
}

.hero-btns .btn {
  margin: 6px;
}

.fa-play-circle {
  margin-left: 4px;
}

.social-icons {
  margin-top: 1.6rem;
  font-size: 1.5rem;
  display: flex;
  justify-content: center;
  align-items: center;
  letter-spacing: 5px;
  
}

.social-icons i{
  color: white;
  font-size: 1.7rem;
  transition: .3s ease;
}

.social-icons i:hover{
  transform: scale(1.15);
  color: rgb(16, 225, 16);
  
}

.social-icons a{
  text-decoration: none;
  color: #fff;
}

.social-icons i{
  padding:0px 10px;
  padding:0vw 1vw;
}


.resume-button{
  margin-top: 2rem;
}


@media screen and (max-width: 768px) {
  .welcome-text{
    width: 85%;
    height: 500px;
  }
  .welcome-text>h1 {
    font-size: 23px;
    margin-top: 30px;
  }

  .welcome-text>h2 {
    margin-top: 5px;
    font-size: 25px;
  }

  .welcome-text>p{
    margin-top: 30px;
    font-size: 15px;
  }

  .social-icons {
    margin-top: 5px;
    font-size: 27px;
  }

  .social-icons a{
    padding: 0px 8px;
  }

  .resume-button{
    margin-top: 30px;
  }
  

  .btn-mobile {
    display: block;
    text-decoration: none;
  }

  .btn {
    width: 100%;
  }
}
