.projects_container {
  background: rgba(15, 15, 15);
  backdrop-filter: blur(10px);
  -webkit-backdrop-filter: blur(10px);
  padding: 4rem 0;
  color: white;
  width: 100%;
  font-family: 'Roboto', monospace;
}

.projects_container .project_container_title {
  text-align: center;
  font-family: monospace;
  font-size: 2.5rem;
  margin-bottom: 4rem;
  letter-spacing: 1px;
}

/* Cards */
.projects_container .postcard {
  display: flex;
  flex-direction: column;
  /* Glass / frosted effect */
  background: rgba(95, 95, 95, 0.05);
  backdrop-filter: blur(12px);
  -webkit-backdrop-filter: blur(12px);
  /* Rounded + glossy border */
  border: 1px solid rgba(255, 255, 255, 0.179);
  /* Shadow / depth */
  box-shadow: 0 8px 25px rgba(0, 0, 0, 0.3), inset 0 0 15px rgba(255, 255, 255, 0.05);
  
  border-radius: 16px;
  overflow: hidden;
  margin-bottom: 2rem;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
  backdrop-filter: blur(8px);
  position: relative; /* needed for absolute tagbox */
  padding-bottom: 0.7rem; /* ensure space for bottom-left tags */
}

/* Hover effect */
.projects_container .postcard:hover {
  transform: translateY(-6px);
  box-shadow: 0 0 30px rgba(16, 225, 16, 0.5);
}

/* Image */
.projects_container .postcard__img {
  width: 100%;
  height: 220px;
  object-fit: cover;
  transition: transform 0.4s ease;
}

.projects_container .postcard:hover .postcard__img {
  transform: scale(1.06);
}

/* Text container */
.projects_container .postcard__text {
  padding: 1.5rem;
  display: flex;
  flex-direction: column;
}

/* Reset all links inside cards */
.projects_container .postcard__text a {
  color: inherit;
  text-decoration: none;
  transition: color 0.3s ease;
}

.projects_container .postcard__text a:hover {
  color: #10e110;
}

/* Title */
.projects_container .postcard__title {
  font-size: 1.7rem;
  font-weight: 700;
  margin-bottom: 0.5rem;
  line-height: 1.2;
}

.projects_container .postcard:hover .postcard__title {
  color: #10e110;
}

/* Subtitle / Tags */
.projects_container .postcard__subtitle {
  font-size: 0.9rem;
  color: #ddd; /* slightly lighter than #aaa */
  margin-bottom: 1rem;
  display: flex;
  flex-wrap: wrap;
  gap: 0.5rem;
  align-items: center;
  font-family: 'Roboto', monospace;
}

.projects_container .postcard__subtitle time {
  background: rgba(16, 225, 16, 0.08);
  padding: 4px 8px;
  border-radius: 12px;
  font-weight: 500;
  transition: all 0.3s ease;
}


.projects_container .postcard__subtitle time:hover {
  background: rgba(16, 225, 16, 0.18);
  transform: translateY(-1px);
}


/* Description */
.projects_container .postcard__preview-txt {
  font-size: 1rem;
  line-height: 1.6;
  color: #ddd;
  margin-bottom: 1.5rem;
}

/* Tags / Tech Stack - bottom-left positioning */
.projects_container .postcard__tagbox {
  position: absolute; /* position inside card */
  bottom: 1.2rem;       /* distance from bottom */
  left: 50%;          /* start at center */
  transform: translateX(-50%); /* truly center horizontally */
  display: flex;
  flex-wrap: wrap;
  gap: 0.5rem;
  list-style: none;   /* remove bullets */
  margin: 0;
  padding: 0;
  justify-content: center; /* center items if multiple tags */
}


.projects_container .postcard__tagbox .tag__item {
  background: rgba(16, 225, 16, 0.12);
  color: #10e110;
  font-size: 0.85rem;
  font-weight: 500;
  padding: 6px 12px;
  border-radius: 20px;
  transition: all 0.3s ease;
}

.projects_container .postcard__tagbox .tag__item:hover {
  background: rgba(16, 225, 16, 0.25);
  transform: translateY(-2px);
}

/* Layout */
.projects_container .container {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(320px, 1fr));
  gap: 2rem;
  padding: 0 10%;
}

@media (max-width: 768px) {
  .projects_container .container {
    padding: 0 6%;
    grid-template-columns: 1fr;
  }

  .projects_container .postcard {
    padding-bottom: 4rem;
  }
}
